<template>
  <div>
    <vs-button @click="printRegistro()" class=" focus:outline-none" border dark>
      <i class="fas fa-file-pdf mr-2"></i> Imprimir Ficha de Postulante
    </vs-button>
  </div>
</template>

<script>
import axios from "@//plugins//axios.js";

export default {
  props: {
    item_selected: {
      type: Object,
    },
  },

  data() {
    return {};
  },

  computed: {
    base_media() {
      let host =
          process.env.NODE_ENV == "development"
              ? process.env.VUE_APP_BASE_URL
              : process.env.VUE_APP_URL;

      host = host.replace("api/v1/", "")
      return host;
    },
  },

  methods: {
    async printRegistro() {
      try {
        const response = await axios.get("reportes/get-ficha-postulante", {
          params: { id: this.item_selected.id },
          responseType: 'blob' // Importante para manejar la respuesta como un archivo
        });

        const url = window.URL.createObjectURL(new Blob([response.data], { type: 'application/pdf' }));
        const a = document.createElement('a');
        a.href = url;
        a.download = `FichaPostulante_${this.item_selected.id}.pdf`; // Nombre del archivo
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
      } catch (error) {
        console.error('There was a problem with the fetch operation:', error);
        alert("Ocurri un problema");
      }
    },
  },
};
</script>

<style lang="scss" scoped></style>
